import React, { Component } from "react";

class AboutMe extends Component {
  render() {
    return (
      <div className="aboutme-text" id="aboutme">
        <h1>About Me</h1>
        <hr />
        <p>
          I am a penultimate Computer Engineering Student in the National
          University of Singapore. My area of interests includes things related
          to Machine Learning, Embedded Systems and the Internet of Things!
        </p>
        <p>
          Currently living in Singapore, I will be moving to Germany for a six
          month internship as a Embedded Systems Engieer at Sonnenglas GmbH
          starting from 17th February 2020.
        </p>
        <p>
          When I am not studying or doing any computing related stuff, my free
          time would be spent on baking, cooking or going to the gym! I am also
          active in volunteering work.
        </p>
      </div>
    );
  }
}

export default AboutMe;
